import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SyntheticDataGeneratorComponent } from './synthetic-data-generator/synthetic-data-generator.component';

const routes: Routes = [
  {
    path: 'synthetic-data-generator',
    component: SyntheticDataGeneratorComponent
  },
  {
    path: '',
    redirectTo: 'synthetic-data-generator',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
