import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class SyntheticDataGeneratorService {

  public syntheticDataUrl: string = environment.apiUrl + '/synthetic-data-generator/api/v1';

  constructor(private http: HttpClient) {}

  getVehicleData(payload: number): Observable<any> {
    return this.http.get(this.syntheticDataUrl + `/vehicle/${payload}`);
  }

  getCustomerData(payload: number): Observable<any> {
    return this.http.get(this.syntheticDataUrl + `/customer/${payload}`);
  }
}
