import {
  ChangeDetectorRef,
  Component,
  OnInit,
  TemplateRef,
  ViewChild,
} from "@angular/core";
import { SyntheticDataGeneratorService } from "../service/synthetic-data-generator.service";
import { FormBuilder, FormGroup } from "@angular/forms";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { MatDialog } from "@angular/material/dialog";
import { LoaderService } from "../service/loader.service";

@Component({
  selector: "app-synthetic-data-generator",
  templateUrl: "./synthetic-data-generator.component.html",
  styleUrls: ["./synthetic-data-generator.component.scss"],
})
export class SyntheticDataGeneratorComponent implements OnInit {
  public searchForm: FormGroup;
  public pageTabList: string[] = ["Vehicle Data", "Customer Data"];
  public pageTabIndex: number = 0;
  public vehicleDataSource: MatTableDataSource<any>;
  public showVehicleData: boolean = false;
  public customerDataSource: MatTableDataSource<any>;
  public showCustomerData: boolean = false;
  public commonData: any = {};
  public commonDialogTitle: string = "";
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild("commonDataDialog") commonDataDialog: TemplateRef<any>;

  constructor(
    private fb: FormBuilder,
    public dialog: MatDialog,
    private loaderService: LoaderService,
    private changeDetector: ChangeDetectorRef,
    private syntheticDataService: SyntheticDataGeneratorService
  ) {}

  ngOnInit(): void {
    this.clearForm();
  }

  initializeForm(): void {
    this.searchForm = this.fb.group({
      searchByNumber: [""],
    });
  }

  fetchSyntheticData(payload): void {
    this.loaderService.show();
    if (this.pageTabIndex === 0) {
      this.fetchVehicleData(payload);
    } else if (this.pageTabIndex === 1) {
      this.fetchCustomerData(payload);
    }
  }

  fetchVehicleData(data: any): void {
    this.loaderService.show();
    this.showVehicleData = false;
    this.vehicleDataSource = new MatTableDataSource([]);
    this.syntheticDataService.getVehicleData(data.searchByNumber).subscribe(
      (response) => {
        if (response && response.length) {
          this.constructDataSource(response);
        } else {
          this.loaderService.hide();
          this.showVehicleData = false;
        }
      },
      (error) => {
        this.loaderService.hide();
        this.showVehicleData = false;
      }
    );
  }

  fetchCustomerData(data: any): void {
    this.loaderService.show();
    this.showCustomerData = false;
    this.customerDataSource = new MatTableDataSource([]);
    this.syntheticDataService.getCustomerData(data.searchByNumber).subscribe(
      (response) => {
        if (response && response.length) {
          this.constructDataSource(response);
        } else {
          this.loaderService.hide();
          this.showCustomerData = false;
        }
      },
      (error) => {
        this.loaderService.hide();
        this.showCustomerData = false;
      }
    );
  }

  constructDataSource(data): void {
    if (this.pageTabIndex === 0) {
      this.showVehicleData = true;

      data.forEach((item) => {
        item['price'] = '€' + item['price'];
        item['odometerReading'] = item['odometerReading'] + ' miles';
      });

      this.vehicleDataSource = new MatTableDataSource(data);
      this.changeDetector.detectChanges();
      this.vehicleDataSource.paginator = this.paginator;
      this.vehicleDataSource.sort = this.sort;
    } else if (this.pageTabIndex === 1) {
      this.showCustomerData = true;
      this.customerDataSource = new MatTableDataSource(data);
      this.changeDetector.detectChanges();
      this.customerDataSource.paginator = this.paginator;
      this.customerDataSource.sort = this.sort;
    }
    this.loaderService.hide();
  }

  isFormValid(): boolean {
    return !this.searchForm.controls["searchByNumber"].value;
  }

  clearForm(): void {
    this.initializeForm();
    this.showVehicleData = false;
    this.showCustomerData = false;
    this.vehicleDataSource = new MatTableDataSource([]);
    this.customerDataSource = new MatTableDataSource([]);
  }

  manageAction(key: number): void {
    this.clearForm();
    this.pageTabIndex = key;
  }

  openDialog(data: object, title: string): void {
    this.commonDialogTitle = this.textFormator(title);
    this.commonData = {};
    if (data) {
      this.commonData = data;
    }
    this.dialog.open(this.commonDataDialog, { panelClass: "common-dialog" });
  }

  fecthObjectKeys(objectData): any {
    if (objectData !== undefined) {
      return Object.keys(objectData);
    }
  }

  textFormator(text: string): string {
    return text.replace(/([A-Z])/g, " $1").replace(/^./, function (str) {
      return str.toUpperCase();
    });
  }

  checkType(data): boolean {
    return Array.isArray(data);
  }

  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    if (this.pageTabIndex === 0) {
      this.vehicleDataSource.filter = filterValue.trim().toLowerCase();
      if (this.vehicleDataSource.paginator) {
        this.vehicleDataSource.paginator.firstPage();
      }
    } else if (this.pageTabIndex === 1) {
      this.customerDataSource.filter = filterValue.trim().toLowerCase();
      if (this.customerDataSource.paginator) {
        this.customerDataSource.paginator.firstPage();
      }
    }
    this.changeDetector.detectChanges();
  }

  allowNumber(e): boolean {
    const charCode = e.which ? e.which : e.keyCode;
    return !(charCode > 31 && (charCode < 48 || charCode > 57));
  }
}
